    }
    .main-index article .recent::-webkit-scrollbar{
        display: none;
    }
    .main-index article .recent ul{
        overflow: auto;
        width: 1080%;
    }
    .main-index article .recent ul li{
        float: left;
        margin-right: 0.1rem;
        width: 1.7rem;
    }
    .main-index article .recent ul li .list-img{
        position: relative;
    }
    .main-index article .recent ul li .list-img img{
        width: 100%;
        height: 2.3rem;
        background: rgba(0,0,0,.5);
    }
    .main-index article .recent ul li .list-img span{
        position: absolute;
        bottom: 0;
        left: 0;
        color:rgb(212, 155, 20);
        font:0.23rem/1.8em "";
        font-weight: bold;
        text-align: center;
        display: inline-block;
        width: 100%;
    }
    .main-index article .recent ul li .list-info p:nth-of-type(1){
        font: 0.3rem/0.5rem "";
        font-weight: bold;
        color:#222222;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        width: 100%;
    }
    .main-index article .recent ul li .list-info p:nth-of-type(2){
        font:0.22rem/0.3rem "";
        color:#fff;
        background: rgb(238, 36, 36);
        border-radius:0.6rem;
        display: inline;
        padding: 0.08rem 0.28rem;
    }
    .main-index article .recent:nth-of-type(2) ul{
        width: 365%;
    }
    .main-index .list-info #date{
        color: #999;
        border: none;
        background: none;
        z-index: 99;
        padding-left: 0;
        width: 100%;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    .nav-panel .panel{
        display: none;
    }
    .nav-panel .panel:nth-of-type(1){
        display: block;
    }
    /*.nav-panel*/
    .nav-panel{

    }
    /*1*/
    .zixun{
        margin-top: 0.3rem;
        border-top:0.1rem solid rgb(248, 248, 248);
    }
    .zixun ul li,.zixun ol li{
        border-bottom:1px solid #eee;
        padding: 0.3rem 0.2rem;
    }
    .zixun ul li dl dt h5{
        font:0.3rem/0.45rem "";
        margin-bottom: 0.1rem;
    }
    .zixun ul li dl dd{
        width: 100%;
        
    }
    .zixun ul li dl dd>img{
        width: 30%;
        margin-right: 2.2%;
        height: 1.2rem;
    }
    .zixun ul li dl dd div,.zixun ol li div{
        font:0.23rem/0.4rem "";
        margin-top: 0.2rem;
        color:rgb(148, 148, 148);
        position: relative;
    }
    .zixun ul li dl dd div span img,.zixun ol li div span img{
        width: 0.3rem;
        position: relative;
        top: 0.06rem;
    }
    .zixun ul li dl dd div span:nth-of-type(2),
    .zixun ol li div span:nth-of-type(2){
        position: absolute;;
        right: 1.3rem;
    }
    .zixun ul li dl dd div span:nth-of-type(2) img,
    .zixun ol li div span:nth-of-type(2) img{
        top:0.08rem;
    }
    .zixun ul li dl dd div span:nth-of-type(3),
    .zixun ol li div span:nth-of-type(3){
        position: absolute;
        right: 0.3rem;
    }
    /**/
    .zixun ul li dl dd video{
        width: 100%;
    }
    /*3*/
    .zixun ol li dl{
        overflow: hidden;

    }
    .zixun ol li dl dt{
        width: 67%;
        float: left;
    }
    .zixun ol li dl dd{
        width: 30%;
        float: right;
    }
    .zixun ol li dl dd img{
        border-radius: 0.1rem;
        width: 100%;
        height: 1.3rem;
    }
    .bottom{
        text-align: center;
        font:0.25rem/0.3rem "";
        padding-top: 0.25rem;
    }
    /**----------------*/
    .pan2 section,.pan4 section{
        border:none;
    }
</style>



