<template>
    <div>
        <h2>
            <ul class="now-list">
                <li v-for="movie in movieList" :key="movie.id" @click='goDetail(movie.id)'>  <!-- @click='goDetail' -->
                   <!---->
                        <div class="movie-img"><img :src="movie.img" alt=""></div>
                        <div class="movie-info">
                            <h5>{{movie.nm}}</h5>
                            <p><span>{{movie.sc}}</span></p>
                            <p>{{movie.star}}</p>
                            <p>{{movie.showInfo}}</p>
                            <router-link to=""></router-link>
                        </div>
                </li>
                <div class='gif' v-show="loadingShow">
                    <img src="../../assets/img/loading.gif" alt="">
                </div>
                <div class='tip' v-show="tip">
                    <p>---------  ---------</p>
                </div>
            </ul>
             
        </h2>
    </div>
</template>

<script>


import Axios from "axios";
export default {
    data(){
        return{
            movieList:[],
            loadingShow:false,
            tip:false
        };
    },
    methods:{
         loaDate(){
         let url1='./static/data/moviedata.json'
        //  let url1='/static/data/moviedata.json'

        Axios.get(url1).then(res=>{
            let list=res.data.data.movies;
            let data=list.slice(this.movieList.length,this.movieList.length+5);
            if(data.length<3){
                this.tip=true;
            }
             this.movieList=this.movieList.concat(data);
            //  console.log(this.movieList);
            }).catch(
                ()=>{alert('movienow-')}
                );   
        },
        goDetail(movieId){
            //id
            this.$router.push('/details/detailscinema/'+movieId);
           /*  this.$router.push('/details/detailscinema/'); */
        }
    },
    mounted(){
        this.loaDate();
        

        window.onscroll=()=>{
            let clientHeight= document.documentElement.clientHeight;//
            let scrollTop=document.documentElement.scrollTop;//
            let scrollHeight=document.documentElement.scrollHeight;//
            if(clientHeight+scrollTop==scrollHeight){
                this.loadingShow=true;
                if(!this.tip){
                    this.loaDate();
                }else{
                    this.loadingShow=false;
                }
            }
        }
    }
};
</script>

<style>
    .now-list{
        margin: 1.8rem 0 1rem 0;
        width: 100%;
        
        overflow: hidden;

    }
    .now-list li{
        padding:0.2rem 0.3rem;
        display: flex;
    }
    .now-list li .movie-img{
        width: 1.5rem;
    }
    .now-list li .movie-img img{
        width: 100%;
    }
    .now-list li .movie-info{
        width: 70%;
        border-bottom: 1px solid #F0F0F0;
        font:0.2rem/0.35rem "";
        padding-left:0.3rem;
        position: relative;
    }
    .now-list li .movie-info h5{
        font:0.33rem/1.5em "";
        font-weight: bold;
        color:#333333;
        width:70%;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;

    }
    .now-list li .movie-info p{
        font:0.25rem/0.48rem "";
        color:#666666;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 75%;

    }
    .now-list li .movie-info p span{
        font-weight: bold;
        color:#FAAF00;
        font-size: 0.3rem;
        margin-left: 0.15rem;
    }
    .now-list li .movie-info a{
        font-size: 0.25rem;
        background: #F03D37;
        color:#FFFFFF;
        padding:0.1rem 0.2rem;
        border-radius: 5px;
        position: absolute;
        right:0rem;
        bottom:0.8rem;
    }
    .gif{
      width:100%;
      text-align: center;
    }
    .tip{
      margin-bottom:0.5rem;
      text-align:center;
      font-size: 0.3rem;
    }
</style>



