<template>
    <div>
        <common-hback>
            <h1 slot="title"></h1> 
        </common-hback>
        <div class="main-sDetails">
            <section class="sec1">
                <dl>
                    <dt><img :src="show.src" alt=""></dt>
                    <dd>
                        <h2>{{show.nm}}</h2>
                        <div>{{show.money}} 
                            <span></span>
                            <img src="../../assets/img/like.jpg" alt="">
                        </div>
                    </dd>
                </dl>
            </section>

            <section class="sec2">
                <p><img src="../../assets/img/time.jpg" alt="">{{show.time}}</p>
                <p>
                    <img src="../../assets/img/addr.jpg" alt="">
                    <span>{{show.addr}}</span>
                    <span>69</span> 
                    <span>&gt;</span>
                </p>
            </section>

            <section class="sec-reset">
                <dl>
                    <dt></dt>
                    <dd>
                         <img src="../../assets/img/more1.jpg" alt="">
                         <p>~</p>
                         <img src="../../assets/img/more2.jpg" alt="">
                         <p>20023/p>
                         <img src="../../assets/img/more3.jpg" alt="">
                         <p>200311612CD</p>
                         <img src="../../assets/img/more4.jpg" alt="">
                         <p>2003612CDLive1022VCD1030DVD</p>
                         <img src="../../assets/img/more5.jpg" alt="">
                         <p></p>
                         <img src="../../assets/img/more6.jpg" alt="">
                         <p> </p>
                         <img src="../../assets/img/more7.jpg" alt="">
                         <p></p>
                         <img src="../../assets/img/more8.jpg" alt="">
                         <p></p>
                         <img src="../../assets/img/more9.jpg" alt="">
                         <p>38Live </p>
                         <img src="../../assets/img/more10.jpg" alt="">
                        <p>Fans</p>
                    </dd>
                </dl>
            </section>

            <section class="sec-reset">
                <dl>
                    <dt></dt>
                    <dd>
                        <img src="../../assets/img/shop.jpg" alt="">
                    </dd>
                </dl>
                
            </section>
        </div>
        <div class="foot-sDetails">
            <h1></h1>
        </div>
    </div>
</template>

<script>
import commonHback from '../common/HeaderBack'
import Axios from 'axios'
export default{
    data(){
        return{
            show:[],
        }
    },
    components:{
        commonHback,
    },
    mounted(){
         let url1 = './static/data/showdetails1.json'
            Axios.get(url1)
            .then(res=>{
                this.show = res.data.showdetails[0];
                // console.log(this.show)
            }).catch(
                ()=>{alert("details-show")}
            )
    }
}
</script>

<style>
    .main-sDetails{
        margin: 1rem 0;
        width: 100%;
        height: 100%;
        position: relative;
        background: #F3f3f3;

    }
    .main-sDetails section{
        margin-bottom: 0.25rem;
        background: #fff;
        padding: 0.2rem;
    }
    .foot-sDetails{
        height: 1rem;
        width: 100%;
        background: rgb(196, 27, 27);
        z-index: 9;
        position: fixed;
        bottom: 0;
        left: 0;
    }
    .foot-sDetails h1{
        font: 0.35rem/1rem "";
        text-align: center;
        font-weight: bold;
        color:#fff;
    }
    .sec1 dl{
        display: flex;
    }
    .sec1 dl dt{
        width: 30%;
    }
    .sec1 dl dd{
        padding-left: 0.3rem;
    }
    .sec1 dl dd h2{
        font:0.34rem/0.5rem "";
        font-weight: bold;
    }
    .sec1 dl dd div{
        font:0.33rem/1rem "";
        font-weight: bold;
        color:#f00;
        position: relative;
        top:0.3rem;
    }
    .sec1 dl dd div span{
        color:orange;
        border:1px solid orange;
        font: 0.22rem/0.22rem "";
    }
    .sec1 dl dd div img{
        position: absolute;
        right:0;
        bottom:0.3rem;
    }
    .sec2 p:nth-of-type(1){
        font:0.25rem/0.7rem "";
        border-bottom:1px solid #eeeeee;
        
    }
    .sec2 p:nth-of-type(1) img{
        position: relative;
        top: 0.2rem;
        padding-right: 0.1rem;
    }
    .sec2 p:nth-of-type(2){
        position: relative;
    }
    .sec2 p:nth-of-type(2) img{
        position: absolute;
        top: 0.1rem;
        left: 0;
    }
    .sec2 p:nth-of-type(2) span{
        display:block;
        margin: 0.1rem 0 0 0.4rem;
    }
    .sec2 p:nth-of-type(2) span:nth-of-type(2){
        font: 0.2rem/0.2rem "";
        color:rgb(201, 198, 198);
    }
    .sec2 p:nth-of-type(2) span:nth-of-type(3){
        position: absolute;
        right: 0.1rem;
        bottom: 0.1rem;
        font-size:0.35rem;
        font-weight: bold;
        color:#999;
        font-family: "";
    }
    .sec-reset{
    }
    .sec-reset dl dt{
        font:0.29rem/0.5rem "";
        color:#333;
        font-weight: bold;
        border-bottom:1px solid #eee;
        padding-bottom:0.2rem;
    }
    .sec-reset dl dd{
        width: 100%;
        overflow: hidden;
    }
    .sec-reset dl dd img{
        width: 100%;
        margin-left: -0.02rem;
    }
    .sec-reset dl dd p{
        padding-top: 0.1rem;
        font:0.25rem/0.4rem "";
    }
</style>


